@keyframes ptf_animate_icon_right {
	49% {
		transform: translateX(100%);
	}

	50% {
		opacity: 0;

		transform: translateX(-100%);
	}

	51% {
		opacity: 1;
	}
}

@keyframes ptf_animate_icon_left {
	49% {
		opacity: 0;

		transform: translateX(-50%);
	}

	50% {
		opacity: 0;

		transform: translateX(50%);
	}

	51% {
		opacity: 1;
	}
}

