@keyframes ptf_fade_in_down {
	0% {
		transform: translate3d(0, -100%, 0);
	}

	100% {
		transform: translate3d(0, 0, 0);
	}
}

@keyframes ptf_fade_out_up {
	0% {
		transform: translate3d(0, 0, 0);
	}

	100% {
		transform: translate3d(0, -100%, 0);
	}
}

@keyframes ptf_blink_in {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

