@keyframes ptf_bounce {

	0%,
	20%,
	50%,
	80%,
	100% {
		transform: translateY(0);
	}

	40% {
		transform: translateY(-20px);
	}

	60% {
		transform: translateY(-10px);
	}
}

// 애니메이션

@keyframes text-clip {
	from {
	  clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
	}
	to {
	  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
	}
  }


@keyframes outer-left-01 {
	from {
	  transform: translateX(15%);
	}
	to {
	  transform: none;
	}
  }
  
  @keyframes outer-left-02 {
	from {
	  transform: translateX(25%);
	}
	to {
	  transform: none;
	}
  }

  @keyframes outer-left-03 {
	from {
	  transform: translateX(35%);
	}
	to {
	  transform: none;
	}
  }

  .line-01 {
	@media all and (min-width: 768px){
		animation: outer-left-01 1s 1s ease both,
		text-clip 2s 0s cubic-bezier(0.5, 0, 0.1, 1) both;
	}
  }

  .line-02 {
	@media all and (min-width: 768px){
		animation: outer-left-02 1s 1s ease both,
		text-clip 2s 0s cubic-bezier(0.5, 0, 0.1, 1) both;
	}
  }

  .line-03 {
	@media all and (min-width: 768px){
		animation: outer-left-03 1s 1s ease both,
		text-clip 2s 0s cubic-bezier(0.5, 0, 0.1, 1) both;
	}
  }

  @keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
